<template><section><h1>modalMore</h1>
<hr>
<h3 id="gai-shu"><a class="header-anchor" href="#gai-shu" aria-hidden="true">¶</a> 概述</h3>
<ul>
<li>多窗体modal组件，目的是为了实现多窗体-提供拖拽、最大化、最小化、还原、关闭、resize</li>
</ul>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<ul>
<li>示例（代码内容基本解释了用法）来自于不同类型的或相同类型的窗体，我们将已显示的窗体进行主键重组统一维护，并提供最小化到地址栏</li>
</ul>
<div class="demo-block">
  <HButton @click="open({'type':'formView','id':'1','displayName':'fv1','stopWorkspaceLeft':true,'moreData':'...'})">
    fromView1-靠左
  </HButton>
  <HButton @click="open({'type':'formView','id':'2','displayName':'fv2','stopDocBottom':true,'moreData':'...'})">
    fromView2-靠下
  </HButton>
  <HButton @click="open({'type':'tableView','id':'1','displayName':'tb1','stopDocTop':300,'moreData':'...'})">
    tableView1-top300
  </HButton>
  <HButton @click="open({'type':'tableView','id':'2','displayName':'tb2','moreData':'...'})">
    tableView2-默认top100-工作区水平居中，高度auto
  </HButton>
  ......
</div>
<!-- 不同类型的模态框 -->
<h-modal-more v-for="(item,index) in viewData.formView" :key="item.feMainId" :ref="item.feMainId" :widgetShowProps="item.isShow" :widgetTitle="item.displayName" :widgetIconStyle="{iconClass:'h-icon-app'}" :stopWorkspaceLeft="item.stopWorkspaceLeft" :stopDocBottom="item.stopDocBottom" @widgetSmall="(item.widgetSmall)(item,index)" @widgetClose="(item.widgetClose)(item.type,index)" getDocTopdomid="common-header" getDocLeftdomid="app-main-left" getDocRightdomid="app-main-right" :widgetSize="{defaultH:300,maxH:500}">
  <template v-slot:widgetBody>
    hello 大家好，我是来自于 类型：formView模态框，配置了默认高度300和最大高度500，并且可以根据循数据各自定义各项配置，例如：我都第一个兄弟是停靠在工作区左侧，第二个兄弟是停靠在document底部
  </template>
</h-modal-more>
<h-modal-more v-for="(item,index) in viewData.tableView" :key="item.feMainId" :ref="item.feMainId" :widgetShowProps="item.isShow" :widgetTitle="item.displayName" :widgetIconStyle="{iconType:'h-icon-app'}" :stopDocTop="item.stopDocTop" @widgetSmall="(item.widgetSmall)(item,index)" @widgetClose="(item.widgetClose)(item.type,index)" getDocTopdomid="common-header" getDocLeftdomid="app-main-left" getDocRightdomid="app-main-right">
  <template v-slot:widgetBody>
     hello 大家好，我是来自于 类型：TableView模态框，默认宽（工作区宽度保留10px间距）高，并且可以根据循数据各自定义各项配置，例如：我都第一个兄弟距离document距离300
  </template>
</h-modal-more>
<!-- 状态bar -->
<div class="status-bar" v-if="viewStatusBar.length>0">
  <div class="item" v-for="(item,index) in viewStatusBar" @click="sendSubviewEmit(item.feMainId,index)"> {{item.feMainId.split("#")[0]}} </div>
</div>


<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;template&gt;\n  &lt;div&gt;\n    &lt;hbutton @click=\&quot;open({'type':'formView','id':'1','displayName':'fv1','stopWorkspaceLeft':true,'moreData':'...'})\&quot;&gt;\n      fromView1-靠左\n    &lt;/hbutton&gt;\n    &lt;hbutton @click=\&quot;open({'type':'formView','id':'2','displayName':'fv2','stopDocBottom':true,'moreData':'...'})\&quot;&gt;\n      fromView2-靠下\n    &lt;/hbutton&gt;\n    &lt;hbutton @click=\&quot;open({'type':'tableView','id':'1','displayName':'tb1','stopDocTop':300,'moreData':'...'})\&quot;&gt;\n      tableView1-top300\n    &lt;/hbutton&gt;\n    &lt;hbutton @click=\&quot;open({'type':'tableView','id':'2','displayName':'tb2','moreData':'...'})\&quot;&gt;\n      tableView2-默认top100-工作区水平居中，高度auto\n    &lt;/hbutton&gt;\n\n    &lt;!-- 不同类型的模态框 --&gt;\n    &lt;h-modal-more v-for=\&quot;(item,index) in viewData.formView\&quot; :key=\&quot;item.feMainId\&quot; :ref=\&quot;item.feMainId\&quot; :widgetshowprops=\&quot;item.isShow\&quot; :widgettitle=\&quot;item.displayName\&quot; :widgeticonstyle=\&quot;{iconClass:'h-icon-app'}\&quot; :stopworkspaceleft=\&quot;item.stopWorkspaceLeft\&quot; :stopdocbottom=\&quot;item.stopDocBottom\&quot; @widgetsmall=\&quot;(item.widgetSmall)(item,index)\&quot; @widgetclose=\&quot;(item.widgetClose)(item.type,index)\&quot; getdoctopdomid=\&quot;common-header\&quot; getdocleftdomid=\&quot;app-main-left\&quot; getdocrightdomid=\&quot;app-main-right\&quot; :widgetsize=\&quot;{defaultH:300,maxH:500}\&quot;&gt;\n      &lt;template v-slot:widgetbody&gt;\n        hello 大家好，我是来自于 类型：formView模态框，配置了默认高度300和最大高度500，并且可以根据循数据各自定义各项配置，例如：我都第一个兄弟是停靠在工作区左侧，第二个兄弟是停靠在document底部\n      &lt;/template&gt;\n    &lt;/h-modal-more&gt;\n    &lt;h-modal-more v-for=\&quot;(item,index) in viewData.tableView\&quot; :key=\&quot;item.feMainId\&quot; :ref=\&quot;item.feMainId\&quot; :widgetshowprops=\&quot;item.isShow\&quot; :widgettitle=\&quot;item.displayName\&quot; :widgeticonstyle=\&quot;{iconType:'h-icon-app'}\&quot; :stopdoctop=\&quot;item.stopDocTop\&quot; @widgetsmall=\&quot;(item.widgetSmall)(item,index)\&quot; @widgetclose=\&quot;(item.widgetClose)(item.type,index)\&quot; getdoctopdomid=\&quot;common-header\&quot; getdocleftdomid=\&quot;app-main-left\&quot; getdocrightdomid=\&quot;app-main-right\&quot;&gt;\n      &lt;template v-slot:widgetbody&gt;\n        hello 大家好，我是来自于 类型：TableView模态框，默认宽（工作区宽度保留10px间距）高，并且可以根据循数据各自定义各项配置，例如：我都第一个兄弟距离document距离300\n      &lt;/template&gt;\n    &lt;/h-modal-more&gt;\n    &lt;!-- 状态bar --&gt;\n    &lt;div class=\&quot;status-bar\&quot; v-if=\&quot;viewStatusBar.length&gt;0\&quot;&gt;\n      &lt;div class=\&quot;item\&quot; v-for=\&quot;(item,index) in viewStatusBar\&quot; @click=\&quot;sendSubviewEmit(item.feMainId,index)\&quot;&gt; {{item.feMainId.split(\&quot;#\&quot;)[0]}} &lt;/div&gt;\n    &lt;/div&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n&lt;/head&gt;&lt;body&gt;&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head><template>
  <div>
    <hbutton @click="open({'type':'formView','id':'1','displayName':'fv1','stopWorkspaceLeft':true,'moreData':'...'})">
      fromView1-靠左
    </hbutton>
    <hbutton @click="open({'type':'formView','id':'2','displayName':'fv2','stopDocBottom':true,'moreData':'...'})">
