<pre v-pre><code v-pre class="language-javascript">&lt;script&gt;
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
     data(){
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">viewData</span>:{
          <span class="hljs-attr">formView</span>:[],
          <span class="hljs-attr">tableView</span>:[]
        },
        <span class="hljs-attr">viewStatusBar</span>:[]
      }
    },
    <span class="hljs-attr">methods</span>: {
      open(data){
        <span class="hljs-keyword">if</span>(!<span class="hljs-keyword">this</span>.getIncludeId(<span class="hljs-keyword">this</span>.viewData[data.type],data)) {
          <span class="hljs-comment">// 将参数传给子View</span>
          data.isShow = <span class="hljs-literal">true</span>
          <span class="hljs-comment">// 关闭回调</span>
          data.widgetClose= <span class="hljs-function">(<span class="hljs-params">type,index</span>)=&gt;</span>{
            <span class="hljs-comment">// - bar状态</span>
            <span class="hljs-keyword">this</span>.viewStatusBar.forEach(<span class="hljs-function">(<span class="hljs-params">ele,i,ary</span>)=&gt;</span>{
              ele.feMainId === <span class="hljs-keyword">this</span>.viewData[type][index].feMainId &amp;&amp; ary.splice(i,<span class="hljs-number">1</span>)
            })
            <span class="hljs-keyword">this</span>.viewData[type].splice(index,<span class="hljs-number">1</span>)
          }
          <span class="hljs-comment">// 最小化回调</span>
          data.widgetSmall = <span class="hljs-function">(<span class="hljs-params">arr</span>) =&gt;</span> {
            <span class="hljs-comment">// + bar状态</span>
            !<span class="hljs-keyword">this</span>.dealRepeat(arr,<span class="hljs-string">"feMainId"</span>) &amp;&amp; <span class="hljs-keyword">this</span>.viewStatusBar.push(arr)
            <span class="hljs-comment">// .....</span>
          }
          <span class="hljs-comment">// 类型不同或相同的窗体 - 前端自定义所有子已经显的窗体添加主键id--用来唯一性区分</span>
          data.feMainId = <span class="hljs-string">`<span class="hljs-subst">${data.type}</span>-<span class="hljs-subst">${data.displayName}</span>#<span class="hljs-subst">${<span class="hljs-built_in">Date</span>.parse(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>())}</span>`</span>
          <span class="hljs-comment">// 给对应类型窗体添加显示数据</span>
          <span class="hljs-keyword">this</span>.viewData[data.type].push(data)
        }
      },
      <span class="hljs-comment">// 判断是否重新打开</span>
      getIncludeId(list,data){
          <span class="hljs-keyword">let</span> flag = <span class="hljs-literal">false</span>
          list.forEach(<span class="hljs-function"><span class="hljs-params">item</span> =&gt;</span> {
            <span class="hljs-comment">// 不管用一个id区分还是多个字段识别，能区分出来当前点击出来的弹出框即可</span>
            <span class="hljs-keyword">if</span>(item.id === data.id &amp;&amp; item.type === data.type) flag = <span class="hljs-literal">true</span>
          })
          <span class="hljs-built_in">console</span>.log(list,data)
          <span class="hljs-keyword">return</span> flag
      },
      <span class="hljs-comment">// 判断是否重复-hxj</span>
      dealRepeat(arr,name){
        <span class="hljs-keyword">let</span> flag = <span class="hljs-literal">false</span>
        <span class="hljs-keyword">if</span>(<span class="hljs-keyword">this</span>.viewStatusBar.length &lt;= <span class="hljs-number">0</span>) <span class="hljs-keyword">return</span> flag
        <span class="hljs-keyword">this</span>.viewStatusBar.forEach(<span class="hljs-function">(<span class="hljs-params">ele</span>)=&gt;</span>{
          arr[name] === ele[name] &amp;&amp; (flag = <span class="hljs-literal">true</span>)
        })
        <span class="hljs-keyword">return</span> flag
      },
      <span class="hljs-comment">// 正常化模态框-hxj</span>
      sendSubviewEmit(feMainId,index){
        <span class="hljs-keyword">this</span>.$refs[feMainId][<span class="hljs-number">0</span>].widgetNormal()
        <span class="hljs-keyword">this</span>.viewStatusBar.splice(index,<span class="hljs-number">1</span>)
      }
    }
  }
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
<pre v-pre><code v-pre class="language-css">&lt;<span class="hljs-selector-tag">style</span> <span class="hljs-selector-tag">lang</span>="<span class="hljs-selector-tag">stylus</span>" <span class="hljs-selector-tag">scoped</span>&gt;
<span class="hljs-selector-class">.status-bar</span>{
  <span class="hljs-attribute">position</span>: fixed;
  <span class="hljs-attribute">bottom</span>: <span class="hljs-number">0</span>;
  <span class="hljs-attribute">right</span>: <span class="hljs-number">0</span>;
  <span class="hljs-attribute">left</span>:<span class="hljs-number">260px</span>;
  <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
  <span class="hljs-attribute">z-index</span>: <span class="hljs-number">9999</span>;
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">0</span> <span class="hljs-number">4px</span>;
  <span class="hljs-attribute">white-space</span>: nowrap;
  <span class="hljs-attribute">overflow-y</span>: auto;
  <span class="hljs-attribute">transition</span>: all .<span class="hljs-number">5s</span> <span class="hljs-built_in">cubic-bezier</span>(.55,0,.1,1);
  <span class="hljs-comment">/*定义滑块 内阴影+圆角*/</span>
  &amp;::-webkit-scrollbar-thumb {
    <span class="hljs-attribute">background-color</span>: <span class="hljs-built_in">rgb</span>(240, 187, 205);
  }
  &amp;<span class="hljs-selector-pseudo">::-webkit-scrollbar-thumb</span><span class="hljs-selector-pseudo">:hover</span> {
    <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#515a6e</span>;
  }
  &amp;<span class="hljs-selector-pseudo">::after</span>{
    <span class="hljs-attribute">content</span>: <span class="hljs-string">""</span>;
    <span class="hljs-attribute">position</span>: absolute;
    <span class="hljs-attribute">z-index</span>: -<span class="hljs-number">1</span>;
    <span class="hljs-attribute">left</span>: <span class="hljs-number">0</span>;
    <span class="hljs-attribute">right</span>: <span class="hljs-number">0</span>;
    <span class="hljs-attribute">bottom</span>: <span class="hljs-number">0</span>;
    <span class="hljs-attribute">width</span>: <span class="hljs-number">100%</span>;
    <span class="hljs-attribute">background</span>: <span class="hljs-number">#da1d1d</span>;
    <span class="hljs-attribute">height</span>: <span class="hljs-number">3px</span>;
    <span class="hljs-attribute">animation</span>: move <span class="hljs-number">1s</span> linear;
  } 
  __<span class="hljs-selector-tag">at__keyframes</span> <span class="hljs-selector-tag">move</span> {
    0% {
      <span class="hljs-attribute">left</span>: -<span class="hljs-number">100%</span>;
    }
    100% {
      <span class="hljs-attribute">left</span>: <span class="hljs-number">0</span>;
    }
  }
  <span class="hljs-selector-class">.item</span>{
    <span class="hljs-attribute">display</span>: inline-block;
    <span class="hljs-attribute">padding</span>:<span class="hljs-number">5px</span> <span class="hljs-number">10px</span>;
    <span class="hljs-attribute">border-top-left-radius</span>: <span class="hljs-number">5px</span>;
    <span class="hljs-attribute">border-top-right-radius</span>: <span class="hljs-number">5px</span>;
    <span class="hljs-attribute">background</span>: steelblue;
    <span class="hljs-attribute">margin-right</span>: <span class="hljs-number">4px</span>;
    <span class="hljs-attribute">color</span>: <span class="hljs-number">#fff</span>;
    <span class="hljs-attribute">cursor</span>: pointer;
    <span class="hljs-attribute">user-select</span>: none;
  }
}
&lt;/<span class="hljs-selector-tag">style</span>&gt;

</code></pre>
</div></demo-block>
<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>当前窗体使用数据-（<strong>配合widgetResize回调使用</strong>）</td>
<td>Object</td>
<td>—</td>
<td>{}</td>
<td></td>
</tr>
<tr>
<td>widgetShowProps</td>
<td>是否显示</td>
<td>Boolean</td>
<td>必填</td>
<td>false</td>
<td></td>
</tr>
<tr>
<td>widgetShowMask</td>
<td>是否显示遮罩</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
<td>显示遮罩不支持多窗体</td>
</tr>
<tr>
<td>widgetTitle</td>
<td>窗体title</td>
<td>String</td>
<td>—</td>
<td>test</td>
<td></td>
</tr>
<tr>
<td>widgetIconStyle</td>
<td>窗体title部分icon</td>
<td>Object</td>
<td>—</td>
<td>{ iconType: 'ios-albums', iconClass: 'iconfont icon-loudou' }</td>
<td></td>
</tr>
<tr>
<td>widgetSize</td>
<td>传入尺寸（<strong>每个字段均为Number类型</strong>）</td>
<td>Object</td>
<td>—</td>
<td>{ defaultW: 默认右侧工作区域宽度, defaultH: auto, maxW: 默认右侧工作区域宽度, maxH: 默认右侧工作区域高度, minW: 默认150, minH:默认150 }</td>
<td></td>
</tr>
<tr>
<td>getDocTopdomid</td>
<td>顶部-头DOM id</td>
<td>String</td>
<td>—</td>
<td>common-header</td>
<td>请给自己对应div添加id</td>
</tr>
<tr>
<td>getDocLeftdomid</td>
<td>左侧-菜单DOM id</td>
<td>String</td>
<td>—</td>
<td>view-tree-left</td>
<td>请给自己对应div添加id</td>
</tr>
<tr>
<td>getDocRightdomid</td>
<td>右侧工作区DOM id</td>
<td>String</td>
<td>—</td>
<td>view-tree-right</td>
<td>请给自己对应div添加id</td>
</tr>
<tr>
<td>stopWorkspaceLeft</td>
<td>是否停靠至-工作区左边</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
<td></td>
</tr>
<tr>
<td>stopDocBottom</td>
<td>是否停靠至-窗体底部边</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
<td></td>
</tr>
<tr>
<td>stopDocTop</td>
<td>多窗体距离窗体的top距离-（如果stopDocBottom为true的时候失效）</td>
<td>Number</td>
<td>—</td>
<td>100</td>
<td></td>
</tr>
<tr>
<td>widgetMinShow</td>
<td>是否支持最小化按钮-最小化需要配合widgetSmall回调函数在父页面进行数据集合维护，然后用集合做菜单</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
<td></td>
</tr>
<tr>
<td>widgetClose</td>
<td>关闭回调</td>
<td>Funcion</td>
<td>—</td>
<td>—</td>
<td></td>
</tr>
<tr>
<td>widgetSmall</td>
<td>最小化回调</td>
<td>Funcion</td>
<td>—</td>
<td>—</td>
<td></td>
</tr>
<tr>
<td>widgetResize</td>
<td>多窗体拉伸回调<strong>请务必传入data参数</strong>，参数：{event:事件,data：数据源,size：当前宽高}</td>
<td>Funcion</td>
<td>—</td>
<td>—</td>
<td></td>
</tr>
</tbody>
</table>
</section></template>
<style lang="stylus" scoped>
.status-bar{
  position: fixed;
  bottom: 0;
  right: 0;
  left:260px;
  width: 100%;
  z-index: 9999;
  padding: 0 4px;
  white-space: nowrap;
  overflow-y: auto;
  transition: all .5s cubic-bezier(.55,0,.1,1);
  /*定义滑块 内阴影+圆角*/
  &::-webkit-scrollbar-thumb {
    background-color: rgb(240, 187, 205);
  }
  &::-webkit-scrollbar-thumb:hover {
    background-color: #515a6e;
  }
  &::after{
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    background: #da1d1d;
    height: 3px;
    animation: move 1s linear;
  } 
  @keyframes move {
    0% {
      left: -100%;
    }
    100% {
      left: 0;
    }
  }
  .item{
    display: inline-block;
    padding:5px 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background: steelblue;
    margin-right: 4px;
    color: #fff;
    cursor: pointer;
    user-select: none;
  }
}
</style>
<script>
  export default {
    data(){
      return {
        viewData:{
          formView:[],
          tableView:[]
        },
        viewStatusBar:[]
      }
    },
    methods: {
      open(data){
        if(!this.getIncludeId(this.viewData[data.type],data)) {
          // 将参数传给子View
          data.isShow = true
          // 关闭回调
          data.widgetClose= (type,index)=>{
            // - bar状态
            this.viewStatusBar.forEach((ele,i,ary)=>{
              ele.feMainId === this.viewData[type][index].feMainId && ary.splice(i,1)
            })
            this.viewData[type].splice(index,1)
          }
          // 最小化回调
          data.widgetSmall = (arr) => {
            // + bar状态
            !this.dealRepeat(arr,"feMainId") && this.viewStatusBar.push(arr)
            // .....
          }
          // 类型不同或相同的窗体 - 前端自定义所有子已经显的窗体添加主键id--用来唯一性区分
          data.feMainId = `${data.type}-${data.displayName}#${Date.parse(new Date())}`
          // 给对应类型窗体添加显示数据
          this.viewData[data.type].push(data)
        }
      },
      // 判断是否重新打开
      getIncludeId(list,data){
          let flag = false
          list.forEach(item => {
            // 不管用一个id区分还是多个字段识别，能区分出来当前点击出来的弹出框即可
            if(item.id === data.id && item.type === data.type) flag = true
          })
          console.log(list,data)
          return flag
      },
      // 判断是否重复-hxj
      dealRepeat(arr,name){
        let flag = false
        if(this.viewStatusBar.length <= 0) return flag
        this.viewStatusBar.forEach((ele)=>{
          arr[name] === ele[name] && (flag = true)
        })
        return flag
      },
      // 正常化模态框-hxj
      sendSubviewEmit(feMainId,index){
        this.$refs[feMainId][0].widgetNormal()
        this.viewStatusBar.splice(index,1)
      }
    }
  }
</script>


