<template><section><h1>hBreadcrumb 面包屑</h1>
<hr>
<h3 id="gai-shu"><a class="header-anchor" href="#gai-shu" aria-hidden="true">¶</a> 概述</h3>
<p>显示网站的层级结构，告知用户当前所在位置，以及在需要向上级导航时使用。
通过当前路由和菜单信息做对比，得到,数组对象，支持路由的name和path两种，切记多语言的情况，bcName需要传递多语言后的名字</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<div class="demo-block">
  <h-bread-crumb :breadcrumb="breadcrumb"></h-bread-crumb>
</div>


<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;head&gt;&lt;template&gt;\n    &lt;h-bread-crumb :breadcrumb=\&quot;breadcrumb\&quot;&gt;&lt;/h-bread-crumb&gt;\n  &lt;/template&gt;\n&lt;/head&gt;&lt;body&gt;&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><head><template>
    <h-bread-crumb :breadcrumb="breadcrumb"></h-bread-crumb>
  </template>
</head><body></body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre><code v-pre class="language-html">  <span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">h-bread-crumb</span> <span class="hljs-attr">:breadcrumb</span>=<span class="hljs-string">"breadcrumb"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">h-bread-crumb</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<pre v-pre><code v-pre class="language-js">&lt;script&gt;
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">menuList</span>:{...},
        <span class="hljs-attr">breadcrumb</span>:[
          {
            <span class="hljs-attr">bcName</span>:<span class="hljs-string">"home"</span>,
            <span class="hljs-attr">path</span>:<span class="hljs-string">"/"</span>
          },
          {
            <span class="hljs-attr">bcName</span>:<span class="hljs-string">"hello"</span>,
            <span class="hljs-attr">name</span>:<span class="hljs-string">"message"</span>
          },
        ]
      }
    }，
    mounted() {
      <span class="hljs-keyword">this</span>.initcb();
    },
    <span class="hljs-attr">watch</span>: {
      $route(to, <span class="hljs-keyword">from</span>) {
        <span class="hljs-keyword">this</span>.initcb();
      }
    },
    initcb() {
      <span class="hljs-comment">//根据this.$route和this.menuList 写的递归，得出当前面包屑路径</span>
    }
  }
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-block>
<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>breadcrumb</td>
<td>数据list</td>
<td>Array</td>
<td>—</td>
<td>—</td>
<td>[{bcName:&quot;home&quot;,path:&quot;/&quot;}] or [{bcName:&quot;home&quot;,name:&quot;/&quot;}] 路由支持name和path</td>
</tr>
</tbody>
</table>
</section></template>
<style lang="less" scoped>
</style>
<script>
export default {
  data () {
    return {
      breadcrumb:
      [
        {
          bcName:"home",
          path:"/"
        },
        {
          bcName:"hello",
          name:"message"
        },
      ]
    }
  },
}
</script>


