<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>备注</th>
</tr>
</thead>
<tbody>
<tr>
<td>getPassword</td>
<td>拿到一个密码参数，建议必填</td>
<td>Function</td>
<td>function</td>
<td>非</td>
</tr>
<tr>
<td>isCursor</td>
