<script>
export default {
  data () {
    return {
      msg: 'Welcome to my vue-pay-pwd',
      isCursor:true,//是否开启光标
      getPassword:null,
    }
  },
  methods: {
    payPw(pw){
      this.getPassword = pw
    }
  }
}
</script>


